<template>
  <div>
    <b-row>
      <b-col md="8" class="mx-auto border py-5">
        <h1 class="text-center mt-3"></h1>
        <b-row class="mt-2">
          <b-col md="10" class="mx-auto my-5">
            <!--  -->
            <b-form-group label=" : " label-for="userId">
              <b-input-group>
                <b-form-input
                  id="userId"
                  v-model="userId"
                  trim
                  placeholder="  ."
                  type="email"
                ></b-form-input>
                <b-input-group-text v-on:click="overlapCheck" type="button"
                  ></b-input-group-text
                >
              </b-input-group>
            </b-form-group>

            <!--  -->
            <b-row>
              <b-col>
                <b-form-group
                  label=" : "
                  label-for="userPw"
                  class="mt-3"
                >
                  <b-form-input
                    id="userPw"
                    v-model="userPw"
                    trim
                    placeholder="  ."
                    type="password"
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col>
                <b-form-group
                  label="  : "
                  label-for="userPw2"
                  class="mt-3"
                >
                  <b-form-input
                    id="userPw2"
                    v-model="userPw2"
                    trim
                    placeholder="  ."
                    type="password"
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <small class="m-0 text-info"
                >    8~20 
                .</small
              >
            </b-row>

            <!-- ,  -->
            <b-row>
              <b-col>
                <b-form-group label=" : " label-for="userName" class="mt-3">
                  <b-form-input
                    id="userName"
                    v-model="userName"
                    trim
                    placeholder="  ."
                    type="text"
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col>
                <b-form-group
                  label=" : "
                  label-for="userNickName"
                  class="mt-3"
                >
                  <b-input-group>
                    <b-form-input
                      id="userNickName"
                      v-model="userNickName"
                      trim
                      placeholder="  ."
                      type="text"
                    ></b-form-input>
                    <b-input-group-text v-on:click="overlapCheck2" type="button"
                      ></b-input-group-text
                    >
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-row>

            <!--  -->
            <b-form-group label=" : " label-for="userTel" class="mt-3">
              <b-input-group>
                <b-form-input
                  id="userTel"
                  v-model="userTel"
                  trim
                  placeholder="  . ( -   .)"
                  type="text"
                ></b-form-input>
              </b-input-group>
            </b-form-group>

            <!--  -->
            <div class="mt-3 d-flex jistify-content-between">
              <span class="me-auto"> : </span>
              <b-form-select
                v-on:change="addre2"
                v-model="address1"
                :options="addr1"
                class="btn btn-sm btn-light me-auto"
              ></b-form-select>
              <b-form-select
                v-on:change="addre3"
                v-model="address2"
                :options="addr2"
                class="btn btn-sm btn-light me-auto"
              ></b-form-select>
              <b-form-select
                v-model="address3"
                :options="addr3"
                class="btn btn-sm btn-light"
              ></b-form-select>
            </div>

            <!--  -->
            <b-button-group vertical class="d-flex mt-2">
              <b-button
                size="lg"
                pill
                block
                variant="primary"
                class="mt-2"
                v-on:click="signUp"
                ></b-button
              >
            </b-button-group>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      userId: "",
      userPw: "",
      userPw2: "",
      userName: "",
      userNickName: "",
      userTel: "",
      address1: "",
      address2: "",
      address3: "",
      addr1: [],
      addr2: [{ value: "", text: "//" }],
      addr3: [{ value: "", text: "" }],
      check: false,
      check2: false,
    };
  },
  beforeCreate() {
    if (this.$session.get("coolUserNo") != null) {
      if (this.$session.get("coolRank") == 0) {
        this.$router.push("ManagerPage");
      } else {
        this.$router.push("/MyPage");
      }
    }
  },
  mounted() {
    this.$axios
      .get("http://localhost:9000/addr1")
      .then((res) => {
        this.addr1 = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    validEmail(email) {
      let re =
        /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
      return re.test(email);
    },
    validPassword(email, password) {
      if (!/^[a-zA-Z0-9]{8,20}$/.test(password)) {
        alert("    8~20  .");
        return false;
      }
      let chk_num = password.search(/[0-9]/g);
      let chk_eng = password.search(/[a-z]/gi);
      if (chk_num < 0 || chk_eng < 0) {
        alert("    .");
        return false;
      }
      if (/(\w)\1\1\1/.test(password)) {
        alert("   4    .");
        return false;
      }
      if (password.search(email) > -1) {
        alert("ID     .");
        return false;
      }
      return true;
    },
    validTel(tel) {
      let re = /^((01[1|6|7|8|9])[0-9]+[0-9]{6,7})|(010[0-9][0-9]{7})$/;
      return re.test(tel);
    },
    overlapCheck() {
      if (!this.validEmail(this.userId)) {
        alert("  ");
        this.check = false;
      } else {
        this.$axios
          .get("http://localhost:9000/overlabCheck", {
            params: {
              email: this.userId,
            },
          })
          .then((res) => {
            if (res.data == true) {
              alert("    .");
              this.check = true;
            } else {
              alert("    .");
              this.check = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    overlapCheck2() {
      this.$axios
        .get("http://localhost:9000/overlabCheck2", {
          params: { nickName: this.userNickName },
        })
        .then((res) => {
          if (res.data == true) {
            alert("    .");
            this.check2 = true;
          } else {
            alert("    .");
            this.check2 = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    sign() {
      if (!this.check) {
        alert("  ");
        return false;
      } else {
        if (this.userPw == "") {
          alert("  ");
          return false;
        } else {
          if (this.userPw2 == "") {
            alert("   ");
            return false;
          } else {
            if (this.userPw != this.userPw2) {
              alert("   .");
              return false;
            } else {
              if (!this.validPassword(this.userId, this.userPw)) {
                return false;
              } else {
                if (this.userName == "") {
                  alert("  ");
                  return false;
                } else {
                  if (this.userNickName == "") {
                    alert("  ");
                  } else {
                    if (!this.check2) {
                      alert("  ");
                      return false;
                    } else {
                      if (this.userTel == "") {
                        alert("  ");
                        return false;
                      } else {
                        if (!this.validTel(this.userTel)) {
                          alert("   ");
                          return false;
                        } else {
                          if (this.address1 == "") {
                            alert("  ");
                            return false;
                          } else {
                            return true;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    },
    signUp() {
      if (this.sign()) {
        if (this.address2 == "") {
          this.address2 == "";
        }
        if (this.address3 == "") {
          this.address3 == "";
        }
        this.$axios
          .post("http://localhost:9000/signUp", {
            email: this.userId,
            password: this.userPw,
            name: this.userName,
            nickName: this.userNickName,
            tel: this.userTel,
            addr1: this.address1,
            addr2: this.address2,
            addr3: this.address3,
          })
          .then(() => {
            alert(" ");
            this.$router.push({
              name: "Login",
              query: { toSignUp: "1" },
            });
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    addre2() {
      this.address2 = "";
      this.address3 = "";
      this.$axios
        .get("http://localhost:9000/addr2", {
          params: {
            addr1: this.address1,
          },
        })
        .then((res) => {
          if (res.data.length == 1) {
            this.addr2 = [
              { value: "", text: "//" },
              { value: "", text: "" },
            ];
          } else {
            this.addr2 = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addre3() {
      this.address3 = "";
      this.$axios
        .get("http://localhost:9000/addr3", {
          params: {
            addr1: this.address1,
            addr2: this.address2,
          },
        })
        .then((res) => {
          if (res.data.length == 1) {
            this.addr3 = [
              { value: "", text: "" },
              { value: "", text: "" },
            ];
          } else {
            this.addr3 = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
