<template>
  <div>
    <b-row>
      <b-col md="8" class="mx-auto border">
        <b-row class="mt-2">
          <b-col md="10" class="mx-auto my-5">
            <b-form-group label=" : " label-for="userId">
              <b-form-input
                id="userId"
                v-model="userId"
                trim
                placeholder="  ."
                type="text"
              ></b-form-input>
            </b-form-group>
            <b-form-group label=" : " label-for="userPw" class="mt-3">
              <b-form-input
                id="userPw"
                v-model="userPw"
                trim
                placeholder="  ."
                v-on:keyup.enter="login"
                type="password"
              ></b-form-input>
            </b-form-group>
            <p class="mt-3 text-info cursor" v-on:click="pwFind">
                ?
            </p>
            <b-button-group vertical class="d-flex">
              <b-button
                size="lg"
                pill
                block
                variant="primary"
                class="mt-3"
                v-on:click="login"
                ></b-button
              >
              <b-button
                size="lg"
                pill
                block
                variant="success"
                class="mt-2"
                v-on:click="signUp"
                ></b-button
              >
            </b-button-group>

            <!--   -->
            <div class="mt-5" v-if="findShow == true">
              <hr />
              <h3 class="text-center"> </h3>
              <b-form-group label=" : " label-for="userId">
                <b-form-input
                  id="findId"
                  v-model="findId"
                  trim
                  placeholder="   ."
                  type="text"
                ></b-form-input>
              </b-form-group>
              <b-form-group label=" : " label-for="findName">
                <b-form-input
                  id="findName"
                  v-model="findName"
                  trim
                  placeholder="   ."
                  type="text"
                ></b-form-input>
              </b-form-group>
              <b-form-group label=" : " label-for="findTel">
                <b-form-input
                  id="findTel"
                  v-model="findTel"
                  trim
                  placeholder="   ."
                  type="text"
                ></b-form-input>
              </b-form-group>
              <b-button-group vertical class="d-flex">
                <b-button
                  size="lg"
                  pill
                  block
                  variant="info"
                  class="mt-3"
                  v-on:click="find"
                  > </b-button
                >
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      userId: "",
      userPw: "",
      findShow: false,
      findId: "",
      findName: "",
      findTel: "",
    };
  },
  beforeCreate() {
    if (this.$session.get("coolUserNo") != null) {
      this.$router.push("MyPage");
    }
  },
  methods: {
    login() {
      if (this.userId == "") {
        alert(" ");
      } else if (this.userPw == "") {
        alert(" ");
      } else {
        this.$axios
          .post("http://localhost:9000/login", null, {
            params: {
              email: this.userId,
              password: this.userPw,
            },
          })
          .then((res) => {
            if (res.data == false) {
              alert(",   ");
            } else {
              this.$session.set("coolUserNo", res.data.userNo);
              this.$session.set("coolEmail", res.data.email);
              this.$session.set("coolRank", res.data.rank);
              this.$session.set("coolNickName", res.data.nickName);
              if (
                this.$route.query.toSignUp != 1 ||
                this.$route.query.toSignUp == undefined
              ) {
                this.$router.go(-1);
              } else {
                this.$router.push("/");
              }
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    signUp() {
      this.$router.push("SignUp");
    },
    pwFind() {
      this.findShow = true;
    },
    find() {
      this.$axios
        .post("http://localhost:9000/find", {
          email: this.findId,
          name: this.findName,
          tel: this.findTel,
        })
        .then((res) => {
          if (res.data == "") {
            alert("   . \n   .");
          } else {
            alert(" : " + res.data + " .");
            this.findId = "";
            this.findName = "";
            this.findTel = "";
            this.findShow = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style scoped>
.cursor {
  cursor: pointer;
}
</style>